import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Poems from '../components/Poems.vue'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/poems',
      name: 'Poems',
      component: Poems
    }
  ]
})



