<template>
  <div id="app">
    <div id="panel" class="mdc-theme--primary-bg">
      <div id="particles-js"></div>
      <div>
        <div class="row">
          <span class="mdc-typography--headline2 mdc-theme--on-primary" id="title">Anusha's</span>
        </div>
        <div class="row tabs">
          <div class="tab">
            <router-link to="/poems" class="mdc-typography--button mdc-theme--on-primary">Poems</router-link>
          </div>
          <div class="tab">
            <router-link to="/blog" class="mdc-typography--button mdc-theme--on-primary">Blog</router-link>
          </div>
        </div>
      </div>
    </div>
    <div id="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  mounted () {
    window.particlesJS('particles-js', {
      'particles': {
        'number': {
          'value': 53,
          'density': {
            'enable': true,
            'value_area': 2083.116342047702
          }
        },
        'color': {
          'value': '#1b1e34'
        },
        'shape': {
          'type': 'circle',
          'stroke': {
            'width': 0,
            'color': '#000'
          },
          'polygon': {
            'nb_sides': 6
          },
          'image': {
            'src': 'img/github.svg',
            'width': 100,
            'height': 100
          }
        },
        'opacity': {
          'value': 0.1,
          'random': true,
          'anim': {
            'enable': false,
            'speed': 1,
            'opacity_min': 0.01,
            'sync': false
          }
        },
        'size': {
          'value': 213.07995233322555,
          'random': true,
          'anim': {
            'enable': true,
            'speed': 10,
            'size_min': 40,
            'sync': false
          }
        },
        'line_linked': {
          'enable': false,
          'distance': 200,
          'color': '#ffffff',
          'opacity': 1,
          'width': 2
        },
        'move': {
          'enable': true,
          'speed': 8,
          'direction': 'none',
          'random': false,
          'straight': false,
          'out_mode': 'out',
          'bounce': false,
          'attract': {
            'enable': false,
            'rotateX': 600,
            'rotateY': 1200
          }
        }
      },
      'interactivity': {
        'detect_on': 'canvas',
        'events': {
          'onhover': {
            'enable': false,
            'mode': 'grab'
          },
          'onclick': {
            'enable': false,
            'mode': 'push'
          },
          'resize': true
        },
        'modes': {
          'grab': {
            'distance': 400,
            'line_linked': {
              'opacity': 1
            }
          },
          'bubble': {
            'distance': 400,
            'size': 40,
            'duration': 2,
            'opacity': 8,
            'speed': 3
          },
          'repulse': {
            'distance': 200,
            'duration': 0.4
          },
          'push': {
            'particles_nb': 4
          },
          'remove': {
            'particles_nb': 2
          }
        }
      },
      'retina_detect': true
    }, () => {
      console.log('callback - particles.js config loaded')
    })
  }
}
</script>

<style>
@import "https://unpkg.com/material-components-web@latest/dist/material-components-web.min.css";
@import "https://fonts.googleapis.com/css?family=Roboto:300,400,500";

body {
  margin: 0;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#particles-js {
  position: fixed;
  top: 0;
  right: 50%;
  bottom: 0;
  left: 0;
}
#app > div {
  box-sizing: border-box;
  width: 50%;
}
#app > div:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
}
#title {
  width: 256px;
  display: block;
  text-align: center;
}
.tabs {
  width: 240px;
  display: flex;
  justify-content: center;
  padding: 16px 8px;
  flex-direction: column;
}
.tab {
  flex: 1 0 auto;
  text-align: center;
  padding: 2px 8px;
  cursor: pointer;
}
.tab a {
  opacity: 0.5;
  transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}
.tab:hover a {
  opacity: 1;
}
a.active {
  opacity: 1;
}
#content {
  overflow-y: auto;
}
</style>



