<template>
  <div id="poems">
    <div v-for="(poem, i) in poems" :key="i" class="poem" v-if="poem.published">
      <title4>{{ poem.title }}</title4>
      <para2>{{ poem.timestamp }}</para2>
      <para1 v-for="(para, j) in poem.content.split('%newline%')" :key="j">{{ para }}</para1>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    poems () {
      var poems = this.$store.state.models.poems
      return poems
    }
  },
  mounted () {
    this.$store.dispatch('createModel', {name: ['mozerella', 'data', 'poems']})
  }
}
</script>

<style>
#poems {
  padding-left: 72px;
  padding-right: 72px;
  padding-top: 32px;
  padding-bottom: 32px;
}
.poem {
  padding-bottom: 72px;
}
</style>



